<template>
    <div class="profile-page">
      <img class="logo" src="../assets/logo_pointfinder.png" @click="redirectHomePage"/>
      <!-- Image de profil -->
      <div class="profile-header">
        <img src="../assets/icone-utilisateur.png" alt="Profil utilisateur">
        <!-- Texte Username Profil -->
        <h2>{{ username }}'s profil</h2>
        <h3>{{ mail }}</h3>
      </div>
      <div class="logout-button">
        <button @click="logout">
          <img src="../assets/logout-xxl.png" alt="Logout"> Logout
        </button>
      </div>

  
      <!-- Panels pour modification de profil -->
      <div class="panel" id="changeEmail">
        <div @click="togglePanel('changeEmail')" class="panel-button">
          <img :src="activePanel === 'changeEmail' ? require('../assets/chevron-bas.png') : require('../assets/chevron-droit.png')" class="arrow-icon">
          <span>Change my email</span>
        </div>
        <div v-if="activePanel === 'changeEmail'" class="content-container">
          <input type="email" v-model="currentEmail" placeholder="Current email">
          <input type="email" v-model="newEmail" placeholder="New email">
          <input type="email" v-model="confirmEmail" placeholder="Confirm new email">
          <button class="validate-button" @click="updateEmail">Update email</button>
        </div>
      </div>
      <br>
      <div class="panel" id="changePassword">
        <div @click="togglePanel('changePassword')" class="panel-button">
          <img :src="activePanel === 'changePassword' ? require('../assets/chevron-bas.png') : require('../assets/chevron-droit.png')" class="arrow-icon">
          <span>Change my password</span>
        </div>
        <div v-if="activePanel === 'changePassword'" class="content-container">
          <input type="email" v-model="currentEmailPassword" placeholder="Email">
          <input type="password" v-model="newPassword" placeholder="New password">
          <input type="password" v-model="confirmPassword" placeholder="Confirm new password">
          <button class="validate-button" @click="updatePassword">Update password</button>
        </div>
      </div>
      <br>
      <div class="panel" id="deleteAccount">
        <div @click="togglePanel('deleteAccount')" class="panel-button">
          <img :src="activePanel === 'deleteAccount' ? require('../assets/chevron-bas.png') : require('../assets/chevron-droit.png')" class="arrow-icon">
          <span>Delete account</span>
        </div>
        <div v-if="activePanel === 'deleteAccount'" class="content-container">
          <input type="email" v-model="emailToDelete" placeholder="Your email">
          <input type="password" v-model="passwordToDelete" placeholder="Your password">
          <button class="validate-button" @click="deleteAccount">Delete account</button>
        </div>
      </div>
    </div>
  </template>
  <script>
    import { useToast } from 'vue-toastification'
    export default {
        setup() {
          // Get toast interface
          const toast = useToast();

          return { toast }
        },
        data() {
          return {
              activePanel: null,
              currentEmail: '',
              newEmail: '',
              confirmEmail: '',
              currentEmailPassword: '',
              newPassword: '',
              confirmPassword: '',
              emailToDelete: '',
              passwordToDelete: ''
          };
        },
        computed: {
          username() {
            const user = JSON.parse(sessionStorage.getItem('user'));
            return user ? user.username : 'Guest';
          },
          mail() {
            const user = JSON.parse(sessionStorage.getItem('user'));
            return user ? user.mail : 'No email';
          }
        },
        methods: {
          togglePanel(panelId) {
              this.activePanel = this.activePanel === panelId ? null : panelId;
          },
          async updateEmail() {
            // Logique pour mettre à jour l'email
            if(this.newEmail === this.confirmEmail){
              try {
                const response = await fetch('https://pointfinder.alwaysdata.net/updatemail', {
                  method: 'PUT',
                  headers: {
                    'Content-Type': 'application/json',
                  },
                  body: JSON.stringify({
                    mail: this.currentEmail,
                    newmail: this.newEmail,
                  }),
                });
                if (response.status === 200) {
                  console.log(this.newEmail);
                  this.toast.success("Email address successfully updated !", {
                    position: "top-right",
                    timeout: 5000,
                    closeButton: "button",
                  });
                } else {
                  this.toast.error("Update email error: please enter an existing email address", {
                    position: "top-right",
                    timeout: 5000,
                    closeButton: "button",
                  });
                }
              } catch (error) {
                console.error('Erreur:', error);
              }
            }
            else{
              this.toast.error("Update email error: email adresses do not match", {
                position: "top-right",
                timeout: 5000,
                closeButton: "button",
              });
            }
          },
          async updatePassword() {
            // Logique pour mettre à jour le mot de passe
            if(this.newPassword === this.confirmPassword){
              try {
                const response = await fetch('https://pointfinder.alwaysdata.net/updatepwd', {
                  method: 'PUT',
                  headers: {
                    'Content-Type': 'application/json',
                  },
                  body: JSON.stringify({
                    mail: this.currentEmailPassword,
                    newpwd: this.newPassword,
                  }),
                });
                if (response.status === 200) {
                  console.log(this.newPassword);
                  this.toast.success("Password successfully updated !", {
                    position: "top-right",
                    timeout: 5000,
                    closeButton: "button",
                  });
                } else {
                  this.toast.error("Update password error: please verify the email you entered", {
                    position: "top-right",
                    timeout: 5000,
                    closeButton: "button",
                  });
                }
              } catch (error) {
                console.error('Erreur:', error);
              }    
            }
            else{
              this.toast.error("Update password error: passwords do not match", {
                position: "top-right",
                timeout: 5000,
                closeButton: "button",
              });
            }
          },
          async deleteAccount() {
              // Logique pour supprimer le compte (il s'agit en réalité d'une désactivation)
              try {
                const response = await fetch('https://pointfinder.alwaysdata.net/inactiveuser', {
                  method: 'PUT',
                  headers: {
                    'Content-Type': 'application/json',
                  },
                  body: JSON.stringify({
                    mail: this.emailToDelete,
                    password: this.passwordToDelete,
                  }),
                });
                if (response.status === 200) {
                  console.log("Compte suspendu");
                  this.logout();
                } else {
                  console.log("Erreur de suspension de compte");
                }
              } catch (error) {
                console.error('Erreur:', error);
              }
          },
          redirectHomePage() {
            this.$router.push({ path: '/' });
          },
          logout() {
            sessionStorage.removeItem('user');
            this.$router.push({ path: '/' });
          }
        }
    }
  </script>
  <style>
    .profile-page {
      padding: 10px; /* Ajoute une marge autour de toute la page */
    }
    .profile-header {
      text-align: center;
    }
    .profile-header img {
        width: 100px;
        height: 100px;
    }
    .panel {
      display: flex;
      flex-direction: column;
      margin-left: 15%;
    }
    .panel-button {
      cursor: pointer;
    }
    .content-container input {
      width: 80%;
      padding: 8px 12px;
      margin: 5px 0;
      border: 1px solid #ccc;
      border-radius: 4px;
    }
    button {
      background-color: rgb(215, 157, 119);
      color: white;
      border: none;
      padding: 10px 20px;
      margin: 10px 0;
      cursor: pointer;
      border-radius: 5px;
    }
    /* Conteneur pour le contenu déroulant */
    .content-container {
      padding: 20px;
      margin-left: 10px;
    }
    .content-container button{
      display: block;
    }
    .arrow-icon {
      width: 20px;
      height: auto;
      margin-right: 10px;
    }
    .validate-button {
      transition: transform 0.3s ease;
    }
    .validate-button:hover {
      transform: scale(1.1);
    }

    .logout-button {
      position: fixed;
      top: 0;
      right: 1em;
    }

    .logout-button button {
      background-color: rgb(114, 114, 114);
      color: white;
      border: none;
      padding: 10px 12px;
      cursor: pointer;
      border-radius: 5px;
      display: flex;
      align-items: center;
      gap: 5px;
      transition: transform 0.3s ease; /* Ajoute une transition douce */
    }

    .logout-button button:hover {
      transform: scale(1.1);
    }

    .logout-button button img {
      width: 20px;
      height: auto;
    }


  </style>
  